/* Common Styles for all Themes */
body {
    font-family: 'Jost', sans-serif;
    transition: background-color 0.5s, color 0.5s;
  }
  
  h1, p {
    margin: 10px 0;
  }
  
  /* Themes with Depth and Jost Font */
  body.theme1, body.theme2, body.theme3, body.theme4, body.theme5, body.theme6, body.theme7, body.theme8, body.theme9, body.theme10 {
    margin: 0;
    padding: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Theme 1 */
  body.theme1 {
    background: linear-gradient(135deg, #f8f8f8, #e6e6e6, #f8f8f8);
    margin: 0;
    padding: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Theme 2 */
  body.theme2 {
    background: linear-gradient(45deg, #ddd, #ccc, #ddd);
    margin: 0;
    padding: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Theme 3 */
  body.theme3 {
    background: linear-gradient(-45deg, #222, #111, #222);
    margin: 0;
    padding: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    color: #fff;
  }
  
  /* Theme 4 */
  body.theme4 {
