    padding: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Theme 7 */
  body.theme7 {
    background: linear-gradient(-45deg, #ffcc99, #ff9966, #ffcc99);
    margin: 0;
    padding: 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Theme 8 */
  body.theme8 {
    background: linear-gradient(45deg, #ffdb58, #ffd700, #ffdb58);
